.elyra-tags {
  margin-top: 8px;
}

.elyra-formEditor-form-tags input {
  margin: 0;
}

.rjsf > .form-group.field,
.form-group.field.tagsField,
.form-group.field.languageField {
  width: 100%;
}

.panel.panel-danger.errors,
p#root__description,
button.btn[type='submit'] {
  display: none;
}

li.text-danger {
  color: var(--jp-error-color1);
  padding: 5px 0 0 9px;
}

form.rjsf {
  width: 100%;
}

fieldset#root > div > fieldset {
  display: flex;
  flex-wrap: wrap;
  min-inline-size: auto;
}

label.control-label {
  font-size: var(--jp-content-font-size1);
  font-weight: 500;
}

fieldset#root {
  display: contents;
}

.form-group.field {
  padding: 0px 30px 15px 0;
  display: flex;
  flex-direction: column;
  width: 50%;
  position: relative;
  height: fit-content;
  min-width: 400px;
}

.elyra-formEditor datalist {
  display: none;
}

.elyra-inputTagList {
  list-style: none;
}

.elyra-inputTag {
  margin-left: 8px;
  margin-right: 8px;
}

button.elyra-inputTag {
  cursor: pointer;
  background: none;
  border: none;
  color: var(--jp-ui-font-color2);
  padding: 0;
  font-size: var(--jp-ui-font-size1);
}

.field-object > fieldset > legend {
  font-size: var(--jp-content-font-size3);
  padding-bottom: 15px;
  font-weight: 600;
}

legend#root__noCategory__title {
  display: none;
}

.form-group input[readonly] {
